@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Helvetica Narrow", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  background-color: #ffffff;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1460px;
  margin: 0 auto;
}

.show-mobile,
.show-tablet {
  display: none;
}

@media (max-width: 1023px) {
  .show-desktop {
    display: none;
  }

  .show-tablet {
    display: block;
  }
}
@media (max-width: 767px) {
  .show-tablet {
    display: none;
  }

  .show-mobile {
    display: block;
  }
}
.header {
  background-color: #1b2d37;
  background-image: url("../../img/header-background-desktop1x.jpg");
  background-size: cover;
  min-height: 700px;
  background-repeat: no-repeat;
  background-position: center;
  color: #ffffff;
}

.header-sightseeing {
  background-image: url("../../img/obzor-background-desktop1x.jpg");
}

.header__wrapper {
  display: flex;
  flex-direction: column;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  padding-left: 50px;
  padding-right: 50px;
}

.header__topLogo {
  margin-right: auto;
}

.nav {
  display: flex;
  gap: 120px;
  list-style: none;
}

.nav__link {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}

.nav__link:hover {
  border-bottom: 3px solid #be0078;
}

.promo {
  display: flex;
  padding: 190px 50px;
}

.promo__title {
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 58px;
  color: #ffffff;
}

@media (max-width: 1023px) {
  .nav {
    gap: 20px;
  }

  .nav__item {
    min-width: 50px;
  }
}
.info {
  background-color: #35008f;
  min-height: 120px;
  color: white;
}

.info__list {
  display: flex;
  list-style: none;
  justify-content: space-between;
  text-align: center;
  padding: 32px 150px 32px;
}

.info__list-item {
  display: flex;
  flex-direction: column;
}

.info__list-number {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 44px;
  letter-spacing: 0.045em;
}

.info__list-text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
}

@media (max-width: 1023px) {
  .info__list {
    padding: 20px;
  }
}
.table {
  padding: 28px 50px 0px;
}

.table-sightseeing {
  padding-top: 25px;
}

.table select,
.table-filter__button {
  background: #fff;
  border: 2px solid #35008f;
  border-radius: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  align-items: center;
  color: #000;
  padding: 6px 15px;
  margin-right: 10px;
}

table-filter__button .table__button {
  background: #ffffff;
  border: 2px solid rgba(70, 0, 190, 0.2);
  box-sizing: border-box;
  border-radius: 10px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  display: flex;
  align-items: center;
  color: #000000;
  padding: 15px 30px;
}

.table__button:hover {
  border: 2px solid #35008f;
  box-sizing: border-box;
  box-shadow: 1px 2px 10px rgba(70, 0, 190, 0.25);
}

.table__header {
  display: flex;
  justify-content: space-between;
  margin-top: -5px;
  margin: auto;
  margin-bottom: 40px;
  max-width: 1052px;
}

.table-filter {
  display: flex;
  margin-top: -5px;
  margin: auto;
  margin-bottom: 40px;
  max-width: 1052px;
}

.table-sightseeing-filter {
  margin-bottom: 30px;
}

.table__button-wrapper {
  display: flex;
  gap: 10px;
  margin-left: 30px;
}

.table__zero {
  width: 160px;
}

.table__list {
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  padding: 0;
  gap: 12px;
  max-width: 1052px;
}

.table-sightseeing__list {
  gap: 10px 76px;
}

@media (max-width: 1023px) {
  .table__zero {
    display: none;
  }

  .table-sightseeing__list {
    gap: 15px;
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .table__list {
    flex-direction: column;
    align-items: center;
  }

  .table-sightseeing__list {
    flex-direction: row;
  }
}
@media (max-width: 520px) {
  .table-sightseeing__list {
    flex-direction: column;
    align-items: center;
  }
}
.table__list-item {
  display: flex;
  flex-direction: column;
  max-width: 49%;
  height: 600px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  margin-bottom: 30px;
}

.table-sightseeing__list-item {
  max-width: 28%;
  height: 418px;
}

.table__list-item-title {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  padding-left: 20px;
  margin-bottom: 0px;
  margin-top: 40px;
}

.table__list-sightseeing-item-title {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  padding-left: 20px;
  margin-bottom: 0px;
  margin-top: 15px;
}

.table__list__item-description {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  padding-left: 20px;
}

.table__list-sightseeing__item-description {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  padding-left: 20px;
  padding-right: 20px;
}

.table__list-button {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  padding: 15px 30px;
  display: flex;
  margin: auto;
  margin-bottom: 20px;
  box-shadow: 1px 2px 10px 1px rgba(53, 0, 143, 0.7);
  border-radius: 10px;
}

@media (max-width: 1023px) {
  .table-sightseeing__list-item {
    max-width: 32%;
    height: auto;
  }
}
@media (max-width: 767px) {
  .table__list-item {
    max-width: 80%;
  }

  .table-sightseeing__list-item {
    max-width: 45%;
  }
}
@media (max-width: 520px) {
  .table-sightseeing__list-item {
    max-width: 70%;
  }
}
.card-info {
  display: flex;
  flex-direction: column;
  min-height: 200px;
  max-width: 1052px;
  margin: 21px auto 60px;
  padding: 60px 30px 40px;
  background: #ffffff;
  box-shadow: 0px 4px 9px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}
.card-info__header {
  display: flex;
}
.card-info__title {
  margin: 0;
  margin-bottom: 40px;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
}
.card-info__subtitle {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: #be0078;
}
.card-info__text {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: black;
}
.card-info__list {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 20px;
}
.card-info__desc {
  margin-top: 35px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
}

.footer {
  min-height: 200px;
  background-color: #35008f;
  padding: 30px 50px;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: white;
}

.footer__avtor {
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 21px;
  width: 74%;
}

.footer__contact {
  display: flex;
  align-items: center;
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  padding-top: 20px;
}

.footer__contact svg {
  margin-right: 10px;
}